	module.exports = {"data":{"challengeNode":{"title":"Use Destructuring Assignment to Pass an Object as a Function's Parameters","description":["在某些情况下，你可以在函数的参数里直接解构对象。","请看以下代码：","<blockquote>const profileUpdate = (profileData) => {<br>&nbsp;&nbsp;const { name, age, nationality, location } = profileData;<br>&nbsp;&nbsp;// 对这些变量执行某些操作<br>}</blockquote>","上面的操作解构了传给函数的对象。这样的操作也可以直接在参数里完成：","<blockquote>const profileUpdate = ({ name, age, nationality, location }) => {<br>&nbsp;&nbsp;/* 对这些参数执行某些操作 */<br>}</blockquote>","这样的操作去除了多余的代码，使代码更加整洁。","这样做还有个额外的好处：函数不需要再去操作整个对象，而仅仅是操作复制到函数作用域内部的参数。","<hr>","对<code>half</code>的参数进行解构赋值，使得仅仅将<code>max</code>与<code>min</code>的值传进函数。"],"challengeType":1,"fields":{"slug":"/javascript-algorithms-and-data-structures/es6/use-destructuring-assignment-to-pass-an-object-as-a-functions-parameters","blockName":"ES6","tests":[{"text":"<code>stats</code>的类型应该是一个<code>object</code>。","testString":"assert(typeof stats === 'object', '<code>stats</code>的类型应该是一个<code>object</code>。');"},{"text":"<code>half(stats)</code>应该等于<code>28.015</code>","testString":"assert(half(stats) === 28.015, '<code>half(stats)</code>应该等于<code>28.015</code>');"},{"text":"使用了解构赋值。","testString":"getUserInput => assert(getUserInput('index').match(/\\(\\s*\\{\\s*\\w+\\s*,\\s*\\w+\\s*\\}\\s*\\)/g), '使用了解构赋值。');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"const stats = {\n  max: 56.78,\n  standard_deviation: 4.34,\n  median: 34.54,\n  mode: 23.87,\n  min: -0.75,\n  average: 35.85\n};\nconst half = (function() {\n  \"use strict\"; // 不要改变这行\n\n  // 在这行以下修改代码\n  return function half(stats) {\n    // 请在函数参数中使用解构赋值\n    return (stats.max + stats.min) / 2.0;\n  };\n  // 在这行以上修改代码\n\n})();\nconsole.log(stats); // 应该为 object\nconsole.log(half(stats)); // 应该为 28.015","head":"","tail":""},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/javascript-algorithms-and-data-structures/es6/create-strings-using-template-literals","id":"587d7b8a367417b2b2512b4d"},"slug":"/javascript-algorithms-and-data-structures/es6/use-destructuring-assignment-to-pass-an-object-as-a-functions-parameters"}}

